

import { Command } from 'commander';
import path from 'path';
import { readFileSync } from 'jsonfile';
import { parse } from 'url';
import { v4 as uuid } from 'uuid';

import runTest from '../tester';
import { CHROMATIC_CREATE_TUNNEL, CHROMATIC_APP_CODE } from '../assets/environment';

// Ensure NODE_ENV is set
process.env.NODE_ENV = process.env.NODE_ENV || 'test';

// This is not exactly clever but it works most of the time
export function findOption(storybookScript, shortName, longName) {
  const parts = storybookScript.split(/[\s='"]+/);
  let index = parts.indexOf(longName);
  if (index === -1) {
    index = parts.indexOf(shortName);
  }
  if (index === -1) {
    return null;
  }
  return parts[index + 1];
}

export function parseArgv(argv) {
  const commander = new Command()
    .option('-a, --app-code [code]', 'the code for your app, get from chromaticqa.com')

    .option('-s, --script-name [name]', 'The npm script that starts your storybook [storybook]')
    .option('-e, --exec [command]', 'Alternatively, a full command to run to start your storybook.')
    .option(
      '-S, --do-not-start',
      "Don't attempt to start; use if your storybook is already running"
    )

    .option(
      '-p, --storybook-port [port]',
      'What port is your Storybook running on (auto detected from -s, if set)?'
    )
    .option(
      '-u, --storybook-url [url]',
      'Storybook is already running at (external) url (implies -S)'
    )
    .option(
      '-d, --storybook-directory [dirname]',
      'Provide a directory with your built storybook, implies -S'
    )

    .option('--ci', 'This build is running on CI, non-interactively (alternatively, pass CI=true)')
    .option('--auto-accept-changes', 'Accept any (non-error) changes or new stories for this build')
    .option(
      '--exit-zero-on-changes',
      "Use a 0 exit code if changes are detected (i.e. don't stop the build)"
    )
    .option('--no-interactive', 'Do not prompt for package.json changes')

    .option('--only [component:story]', 'Only run a single story (for debugging purposes)')
    .option('--debug', 'Output more debugging information')

    // We keep this for back compat it does nothing (ie. it is the default)
    .option('--storybook-addon', '(deprecated) use the storybook addon')
    .parse(argv);

  const commanderOptions = {
    config: commander.config,
    appCode: commander.appCode || CHROMATIC_APP_CODE,
    scriptName: commander.scriptName,
    commandName: commander.exec,
    noStart: !!commander.doNotStart,
    port: commander.storybookPort,
    url: commander.storybookUrl,
    dirname: commander.storybookDirectory,
    only: commander.only,
    fromCI: !!commander.ci,
    autoAcceptChanges: !!commander.autoAcceptChanges,
    exitZeroOnChanges: !!commander.exitZeroOnChanges,
    interactive: !!commander.interactive,
    verbose: !!commander.debug,
    createTunnel: !commander.storybookUrl && CHROMATIC_CREATE_TUNNEL !== 'false',
    originalArgv: argv,
  };

  const packageJson = readFileSync(path.resolve('./package.json'));
  const { commandName } = commanderOptions;
  // eslint-disable-next-line prefer-const
  let { port, url, dirname, noStart, scriptName } = commanderOptions;

  if (scriptName && commandName) {
    throw new Error('Cannot use both --scriptName and --commandName');
  }

  if (url || dirname) {
    if (scriptName || commandName) {
      throw new Error(
        `Cannot use ${scriptName ? `--scriptName` : `--exec`} with ${
          url ? '--storybook-url' : '--storybook-directory'
        }, it implies --do-not-start`
      );
    }

    if (url && dirname) {
      throw new Error(`Cannot use both --storybook-url and --storybook-directory`);
    }

    noStart = true;
  } else {
    if (commandName) {
      if (!port) {
        throw new Error(`You must pass a port with the --storybook-port option when using --exec.`);
      }
    } else {
      scriptName = scriptName || 'storybook';
      const storybookScript = packageJson.scripts && packageJson.scripts[scriptName];

      if (!storybookScript) {
        throw new Error(
          `Chromatic Tester: Didn't find a script called '${scriptName}' in your \`package.json\`.\n` +
            'Make sure you set the `--script-name` option to the value of the npm script that starts your storybook'
        );
      }

      port = port || findOption(storybookScript, '-p', '--port');
      if (!port) {
        throw new Error(
          `Didn't detect a port in your '${scriptName}' script. You must pass a port with the --storybook-port option.`
        );
      }

      // eslint-disable-next-line no-console
      console.log(
        `Chromatic Tester: Detected '${scriptName}' script, running with inferred options:
    --script-name=${scriptName} --storybook-port=${port}
  Override any of the above if they were inferred incorrectly.
  `
      );
    }

    url = `http://localhost:${port}`;
  }

  if (dirname) {
    return { ...commanderOptions, noStart: true, dirname };
  }

  const parsedUrl = parse(url);
  const suffix = 'iframe.html';
  if (!parsedUrl.pathname.endsWith(suffix)) {
    if (!parsedUrl.pathname.endsWith('/')) {
      parsedUrl.pathname += '/';
    }
    parsedUrl.pathname += suffix;
  }

  return { ...commanderOptions, noStart, url: parsedUrl.format(), scriptName };
}

export async function executeTest(argv) {
  const sessionId = uuid();
  try {
    const exitCode = await runTest({
      ...parseArgv(argv),
      sessionId,
    });
    process.exit(exitCode);
  } catch (error) {
    console.error(
      `**Chromatic build failed. Please note the session id: '${sessionId}' and contact support@hichroma.com -or- open a support ticket at https://chromaticqa.com**\n`
    );
    // eslint-disable-next-line no-console
    console.error(error);
    // Not sure what exit code to use but this can mean error.
    process.exit(255);
  }
}

// Normal usage, outside of test
if (require.main === module) {
  executeTest(process.argv);
}
